import React from "react";
import { bool } from "prop-types";
import { StyledMenu } from "./Menu.styled";
import { Typography } from "@material-ui/core";

const Menu = ({ open, ...props }) => {
  const isHidden = open ? true : false;
  const tabIndex = isHidden ? 0 : -1;
  const onMouseOver = (event) => {
    const el = event.target;
    let colorhex = ["#000000"];
    el.style.color = colorhex[0];
  };

  const onMouseOut = (event) => {
    const el = event.target;
    let white = "white";
    el.style.color = white;
  };
  const styles = {
    color: "white",
    fontSize: 1 + "em",
    "@media (max-width: 500px)": {
      display: "none",
    },
  };
  return (
    <StyledMenu open={open} aria-hidden={!isHidden} {...props}>
      <a href="/" tabIndex={tabIndex}>
        {/* <span aria-hidden="true">üíÅüèª‚Äç‚ôÇÔ∏è</span>
        About */}
        <Typography
          variant="overline"
          display="block"
          gutterBottom
          style={styles}
        >
          0.{" "}
          <b
            style={{ textDecoration: "none", color: "white" }}
            onMouseEnter={(event) => onMouseOver(event)}
            onMouseOut={(event) => onMouseOut(event)}
          >
            {" "}
            About
          </b>
        </Typography>
      </a>
      <a href="/cs" tabIndex={tabIndex}>
        <Typography
          variant="overline"
          display="block"
          gutterBottom
          style={styles}
        >
          {" "}
          1.{" "}
          <b
            style={{ textDecoration: "none", color: "white" }}
            onMouseEnter={(event) => onMouseOver(event)}
            onMouseOut={(event) => onMouseOut(event)}
          >
            {" "}
            Coding
          </b>
        </Typography>
      </a>
      <a href="/life" tabIndex={tabIndex}>
        <Typography
          variant="overline"
          display="block"
          gutterBottom
          style={styles}
        >
          {" "}
          2.{" "}
          <b
            style={{
              textDecoration: "none",
              color: "white",
            }}
            onMouseEnter={(event) => onMouseOver(event)}
            onMouseOut={(event) => onMouseOut(event)}
          >
            Life
          </b>
        </Typography>
      </a>
      <a href="/resume" tabIndex={tabIndex}>
        <Typography
          variant="overline"
          display="block"
          gutterBottom
          style={styles}
        >
          3.{" "}
          <b
            style={{ textDecoration: "none", color: "white" }}
            onMouseEnter={(event) => onMouseOver(event)}
            onMouseOut={(event) => onMouseOut(event)}
          >
            Resume
          </b>
        </Typography>
      </a>
    </StyledMenu>
  );
};

Menu.propTypes = {
  open: bool.isRequired,
};

export default Menu;
